.categories-side-server {
  background-color: #fff;
  margin-top: 0.46875rem;
}
.categories-side-server ul.menu {
  padding-top: 0;
  clear: both;
  padding: 0;
  margin: 0;
}
.categories-side-server ul.menu .item {
  padding: 0 0 0 0.9375rem;
  margin: 0 0 0.3125rem 0;
  line-height: 2.03125rem;
  background-color: #FFF5E2;
  overflow: hidden;
  min-height: 2.03125rem;
}
.categories-side-server ul.menu .item a {
  color: #333;
  display: block;
}
.categories-side-server ul.menu .item .text {
  display: inline;
  vertical-align: middle;
}
.categories-side-server ul.menu .item .arrow-tx-right {
  background-image: url('https://img.limeetpet.com/limeet/arrow-right.png');
  background-repeat: no-repeat;
  background-size: 120%;
  width: 0.75rem;
  height: 0.75rem;
  vertical-align: middle;
  float: right;
  margin-right: 0.625rem;
  margin-top: 0.625rem;
}
.categories-side-server ul.menu .item:hover {
  border: none;
  background-color: #F6912D;
  color: #fff;
}
.categories-side-server ul.menu .item:hover a {
  color: #fff;
}
.categories-side-server ul.menu .item:hover a:hover {
  color: #fff;
}
.categories-side-server ul.menu .item:hover .arrow-tx-right {
  background-image: url('https://img.limeetpet.com/limeet/arrow-right2.png');
}
.categories-side-server ul.menu .item.active {
  border: none;
  background-color: #F6912D;
  color: #fff;
}
.categories-side-server ul.menu .item.active a {
  color: #fff;
}
.categories-side-server ul.menu .item.active a:hover {
  color: #fff;
}
.categories-side-server ul.menu .item.active .arrow-tx-right {
  background-image: url('https://img.limeetpet.com/limeet/arrow-right2.png');
}
.categories-side-server a {
  color: #333;
}
.categories-side-server a.active {
  background-color: #FFF5E2;
}
.categories-side-server a:hover {
  background-color: #F6912D;
  color: #fff;
}
